/* src/App.css */

body {
  display: flex;
  justify-content: center;
  background-color: #ffffffee;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  background-color: #ffffff;
  justify-content: center;
  display: flex;
  max-width: 1000px;
  width: 100vw;
  flex-direction: column;
  align-items: center;
}

.App-header {
  width: 100%;
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
  color: #333;
}

.user-stats {
  max-width: 130px;
}

.etf-row {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: -20px; /* Reduced margin-bottom to close the gap */
}

.user-votes {
  margin-top: 30px;
  margin-left: 20px;
  width: 100%;
  text-align: left;
}

@keyframes blip {
  0% { color: rgb(72, 129, 129); }
  50% { background-color: transparent; }
}

.blip {
  animation: blip 0.9s ease;
}


